@import "./_fonts.scss";

// $bg: #f0f2f5;
// $bg2: #ffffff;

$bg: #15191f;
$textcol: whitesmoke;

h1, h2, h3, h4, h5, h6 {
    color: $textcol;
}

// li {
//     color: $textcol !important;
// }

.ant-layout {
    background: transparent;
}

.ant-divider {
    color: $textcol;
    border-top-color: #F0F0F4 !important;
    border-left-color: #F0F0F4 !important;
}

.ant-upload-text {
    color: $textcol !important;
}

.ant-upload-hint {
    color: rgba($textcol, 0.5) !important;
}

.ant-slider-rail {
    background-color: rgba($textcol, 0.1) !important;
}

.ant-breadcrumb {

    a {
        color: rgba($textcol, 0.5);
    }

    a:hover {
        color: #1677ff;
        background-color: rgba($textcol, 0.03);
    }

    li:last-child {
        color: $textcol;
    }
    
    .ant-breadcrumb-separator {
        color: rgba($textcol, 0.5);
    }
}

.ant-anchor-link-title {
    color: $textcol !important;

}

.ant-anchor-link-title-active {
    color: #1677ff !important;
}

.ant-anchor::before {
    border-inline-start-color: rgba($textcol, 0.25) !important;
}

.styled-text {
    color: $textcol;
    font-family: "Raleway", sans-serif !important;
}

body {

    background-color: $bg;
    color: $textcol;

    .loadingPage {
        text-align: center;
        min-height: 90vh;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .navigation {
        background: transparent;
        font-family: "Raleway", sans-serif !important;
        letter-spacing: 0.2em;
        height: 50px;
        line-height: 50px;
    }

    .padded-main {
        margin-top: 50px;
        padding: 1em;
    }

    .main-content {
        background-color: $bg;
    }

    .project-view {
        
        .LangTag {
            border: 1px;
            display: inline-block;
            padding: 0.1em 0.6em;
        }
        
        .ant-card-meta-description
        {
            white-space: nowrap;
            text-overflow: "& Others ...";
            overflow: hidden;
        }

        .ant-card-cover
        {
            height: 200px;
            overflow: hidden;

            .cover-image-container
            {
                height: inherit;
                display: grid;
                position: relative;

                .ant-image
                {
                    width: inherit;
                    grid-area: 1/1;

                    .ant-image-mask
                    {
                        @extend .ant-card-cover;
                    }

                    .ant-image-mask-info::after
                    {
                        content: " Thumbnail";
                    }
                }

                .fade-out
                {
                    height: 20%;
                    width: inherit;
                    grid-area: 1/1;
                    background: linear-gradient(to top, rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
                    position: absolute;
                    bottom: 0;
                }
            }
        }
    }

    

    .App {
        text-align: center;
          
        .App-logo {
            height: 40vmin;
            pointer-events: none;
        }
          
        @media (prefers-reduced-motion: no-preference) {
            .App-logo {
                animation: App-logo-spin infinite 20s linear;
            }
        }
          
        .App-header {
            background-color: #282c34;
            min-height: 100vh;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            font-size: calc(10px + 2vmin);
            color: white;
        }
          
        .App-link {
            color: #61dafb;
        }
          
        @keyframes App-logo-spin {
            from {
                transform: rotate(0deg);
            }
            to {
                transform: rotate(360deg);
            }
        }
    }
}